.table-block {
  background: #ffffff;
  border-radius: 12px;
  padding: 24px;
  box-shadow:
    0 2px 8px rgba(0, 0, 0, 0.08),
    0 8px 24px rgba(0, 0, 0, 0.12);
  transition: all 0.3s ease;
  border: 1px solid #e5e7eb;
  border-left: 4px solid #3b82f6;

  &:hover {
    box-shadow:
      0 4px 16px rgba(0, 0, 0, 0.15),
      0 12px 32px rgba(0, 0, 0, 0.1);
    border-color: #d1d5db;
  }
}

.table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--surface-100);
}

.table-title {
  color: var(--text-color);
  font-size: 1.125rem;
  font-weight: 700;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.table-actions {
  display: flex;
  gap: 8px;
}

.action-btn {
  background: var(--surface-100);
  border: 1px solid var(--surface-200);
  border-radius: 6px;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  color: var(--text-color-secondary);

  &:hover {
    background: var(--primary-50);
    border-color: var(--primary-200);
    color: var(--primary-600);
    transform: scale(1.1);
  }
}

.export-icon,
.fullscreen-icon {
  font-size: 14px;
  font-style: normal;
}

.table-container {
  position: relative;
  transition: all 0.3s ease;

  &.fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--surface-0);
    z-index: 1000;
    padding: 20px;
    border-radius: 0;
  }
}

.table-responsive {
  overflow-x: auto;
  border-radius: 8px;
  border: 1px solid var(--surface-200);
}

.data-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  font-size: 0.875rem;
}

.header-row {
  background: linear-gradient(135deg, var(--surface-100), var(--surface-50));
}

.header-cell {
  padding: 16px 12px;
  text-align: left;
  font-weight: 600;
  color: var(--text-color);
  border-bottom: 2px solid var(--surface-200);
  position: relative;
  user-select: none;
  transition: all 0.2s ease;

  &.sortable {
    cursor: pointer;

    &:hover {
      background: var(--surface-200);
    }
  }

  &.highlight {
    background: linear-gradient(135deg, var(--primary-100), var(--primary-50));
    border-bottom-color: var(--primary-300);
  }

  &.sorted-asc,
  &.sorted-desc {
    background: linear-gradient(135deg, var(--primary-200), var(--primary-100));
  }

  &:first-child {
    border-top-left-radius: 8px;
  }

  &:last-child {
    border-top-right-radius: 8px;
  }
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
}

.header-text {
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.sort-indicator {
  display: flex;
  align-items: center;
  width: 16px;
  height: 16px;
}

.sort-icon {
  font-size: 12px;
  color: var(--text-color-secondary);
  transition: all 0.2s ease;

  &.sort-asc::before {
    content: '▲';
    color: var(--primary-600);
  }
  &.sort-desc::before {
    content: '▼';
    color: var(--primary-600);
  }
  &.sort-none::before {
    content: '⇅';
  }
}

.data-row {
  transition: all 0.2s ease;

  &:hover {
    background: var(--surface-50);
    transform: scale(1.002);
  }

  &.highlight-row {
    background: rgba(0, 0, 0, 0.02);
  }
}

.data-cell {
  padding: 12px;
  border-bottom: 1px solid var(--surface-100);
  transition: all 0.2s ease;

  &.highlight {
    background: var(--primary-25);
    border-left: 3px solid var(--primary-300);
  }

  &.numeric {
    text-align: right;
    font-variant-numeric: tabular-nums;
  }

  &.text {
    text-align: left;
  }
}

.cell-content {
  display: flex;
  align-items: center;
  gap: 8px;
  justify-content: space-between;
}

.cell-value {
  color: var(--text-color);
  font-weight: 500;
}

.cell-trend {
  font-size: 0.75rem;
  padding: 2px 6px;
  border-radius: 12px;
  font-weight: 600;

  &.trend-positive {
    background: #dcfce7;
    color: #166534;
  }

  &.trend-negative {
    background: #fef2f2;
    color: #dc2626;
  }

  &.trend-neutral {
    background: #f3f4f6;
    color: #6b7280;
  }
}

.empty-row {
  .empty-cell {
    padding: 40px;
    text-align: center;
    border: none;
  }

  .empty-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    color: var(--text-color-secondary);

    .empty-icon {
      font-size: 24px;
      opacity: 0.5;
    }

    p {
      margin: 0;
      font-size: 0.875rem;
    }
  }
}

.table-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid var(--surface-200);
  font-size: 0.875rem;
}

.row-count {
  color: var(--text-color-secondary);
  font-weight: 500;
}

.table-summary {
  color: var(--text-color-secondary);

  strong {
    color: var(--text-color);
    font-weight: 600;
  }
}

// Dark Mode Support
:host-context(.dark) {
  .table-block {
    background: var(--surface-800);
  }

  .header-row {
    background: linear-gradient(135deg, var(--surface-700), var(--surface-750));
  }

  .header-cell {
    border-bottom-color: var(--surface-600);

    &.highlight {
      background: linear-gradient(
        135deg,
        rgba(59, 130, 246, 0.2),
        rgba(59, 130, 246, 0.1)
      );
      border-bottom-color: rgba(59, 130, 246, 0.3);
    }

    &.sorted-asc,
    &.sorted-desc {
      background: linear-gradient(
        135deg,
        rgba(59, 130, 246, 0.3),
        rgba(59, 130, 246, 0.2)
      );
    }
  }

  .data-row {
    &:hover {
      background: var(--surface-700);
    }

    &.highlight-row {
      background: rgba(255, 255, 255, 0.02);
    }
  }

  .data-cell {
    border-bottom-color: var(--surface-700);

    &.highlight {
      background: rgba(59, 130, 246, 0.1);
      border-left-color: rgba(59, 130, 246, 0.5);
    }
  }

  .table-footer {
    border-top-color: var(--surface-600);
  }

  .action-btn {
    background: var(--surface-700);
    border-color: var(--surface-600);

    &:hover {
      background: var(--surface-600);
      border-color: var(--surface-500);
    }
  }
}

// Responsive Design
@media (max-width: 768px) {
  .table-block {
    padding: 16px;
  }

  .table-header {
    flex-direction: column;
    align-items: stretch;
    gap: 12px;
  }

  .table-actions {
    align-self: flex-end;
  }

  .header-cell,
  .data-cell {
    padding: 8px;
    font-size: 0.8rem;
  }

  .table-footer {
    flex-direction: column;
    gap: 8px;
    align-items: stretch;
  }

  .table-summary {
    text-align: center;
  }
}
